* {
  font-size: 18px;
  font-family: 'Open Sans', sans-serif;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
.btn-no-style {
  background: none;
  outline: none;
  cursor: pointer;
  border: none;
}
.centered-content {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -moz-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
  -webkit-box-pack: center;
  -moz-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  flex-direction: column;
}
h1 {
  font-size: 64px;
  line-height: 177.68%;
}
h2 {
  font-size: 48px;
  line-height: 177.68%;
}
h3 {
  font-size: 30px;
  line-height: 177.68%;
}
h4 {
  font-size: 24px;
  line-height: 177.68%;
}
h5 {
  font-size: 18px;
  line-height: 177.68%;
  font-weight: normal;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0;
  /* <-- Apparently some margin are still there even though it's hidden */
}
.info {
  margin-top: 7px;
  padding: 0 20px;
  width: 100%;
  font-size: 14px;
  z-index: 10;
}
.info.hidden {
  display: none;
}
form.was-validated input[type="text"]:invalid,
.insurance-services-block input[type="text"]:invalid,
form.was-validated input[type="password"]:invalid,
.insurance-services-block input[type="password"]:invalid,
form.was-validated input[type="email"]:invalid,
.insurance-services-block input[type="email"]:invalid,
form.was-validated input[type="number"]:invalid,
.insurance-services-block input[type="number"]:invalid {
  border-color: #ED462F !important;
}
form.was-validated input[type="text"]:invalid + .info,
.insurance-services-block input[type="text"]:invalid + .info,
form.was-validated input[type="password"]:invalid + .info,
.insurance-services-block input[type="password"]:invalid + .info,
form.was-validated input[type="email"]:invalid + .info,
.insurance-services-block input[type="email"]:invalid + .info,
form.was-validated input[type="number"]:invalid + .info,
.insurance-services-block input[type="number"]:invalid + .info {
  color: #ED462F;
  display: block;
}
form.was-validated .invalid,
.insurance-services-block .invalid {
  border-color: #ED462F !important;
}
form.was-validated .invalid + .info,
.insurance-services-block .invalid + .info {
  color: #ED462F;
  display: block;
}
img {
  font-size: 14px;
}
input[type="text"],
input[type="password"],
input[type="email"],
input[type="number"] {
  display: inline-block;
  height: 35px;
  padding: 0 20px;
  width: 100%;
  border: 1px solid #868686;
  border-radius: 15px;
  outline: none;
}
input[type="text"]:focus,
input[type="password"]:focus,
input[type="email"]:focus,
input[type="number"]:focus {
  border-color: #66D1C6;
}
input[type="text"]::-webkit-input-placeholder,
input[type="password"]::-webkit-input-placeholder,
input[type="email"]::-webkit-input-placeholder,
input[type="number"]::-webkit-input-placeholder {
  font-size: 14px;
}
input[type="text"].show-due + .info.hidden,
input[type="password"].show-due + .info.hidden,
input[type="email"].show-due + .info.hidden,
input[type="number"].show-due + .info.hidden,
input[type="text"].show-tel + .info.hidden,
input[type="password"].show-tel + .info.hidden,
input[type="email"].show-tel + .info.hidden,
input[type="number"].show-tel + .info.hidden {
  display: block;
}
button:not(.btn-no-style) {
  background: none;
  outline: none;
  border: none;
  text-align: center;
  border: 1px solid gray;
  color: gray;
  border-radius: 50px;
  padding: 7px 18px;
  cursor: pointer;
}
button:not(.btn-no-style).red {
  color: #ED462F;
  border-color: #ED462F;
}
button:not(.btn-no-style).red.ready {
  background-color: #ED462F;
  color: white;
}
button:not(.btn-no-style).green {
  color: #66D1C6;
  border-color: #66D1C6;
}
button:not(.btn-no-style).green.ready {
  background-color: #66D1C6;
  color: white;
}
body {
  background: #E5E5E5;
  min-height: 100vh;
  margin: 0;
}
@media screen and (min-width: 500px) {
  body {
    margin: 20px;
    min-height: calc(100vh - 40px);
  }
}
body .centered-content .insurance-form-container {
  width: 967px;
  padding: 55px 95px;
  background: #FFFFFF;
}
@media screen and (min-width: 500px) {
  body .centered-content .insurance-form-container {
    border-radius: 15px;
  }
}
@media screen and (max-width: 840px) {
  body .centered-content .insurance-form-container {
    width: 100%;
    padding: 35px;
  }
}
@media screen and (max-width: 500px) {
  body .centered-content .insurance-form-container {
    padding: 15px;
    min-height: 100vh;
  }
}
.form-group {
  position: relative;
  min-height: 35px;
}
.insurance-header {
  text-align: center;
  margin-top: 0;
  margin-bottom: 73px;
}
@media screen and (max-width: 840px) {
  .insurance-header {
    margin-bottom: 40px;
  }
}
@media screen and (max-width: 500px) {
  .insurance-header {
    margin-bottom: 20px;
    font-size: 22px;
  }
}
::-webkit-scrollbar-button {
  background-image: none;
  background-repeat: no-repeat;
  width: 6px;
  height: 0px;
}
::-webkit-scrollbar-track {
  background: none;
}
::-webkit-scrollbar-thumb {
  -webkit-border-radius: 5px;
  border-radius: 5px;
  background-color: #66D1C6;
}
::-webkit-scrollbar {
  width: 6px;
}
.insurance-main-form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.insurance-main-form .insurance-type-block {
  display: flex;
  justify-content: stretch;
  align-items: stretch;
  border: 1px solid rgba(0, 0, 0, 0.13);
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 53px;
}
@media screen and (max-width: 840px) {
  .insurance-main-form .insurance-type-block {
    margin-bottom: 30px;
  }
}
@media screen and (max-width: 500px) {
  .insurance-main-form .insurance-type-block {
    margin-bottom: 15px;
  }
}
.insurance-main-form .insurance-type-block input[type="radio"] {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.insurance-main-form .insurance-type-block input[type="radio"] + label {
  display: block;
  padding: 12px 28px;
  cursor: pointer;
}
@media screen and (max-width: 500px) {
  .insurance-main-form .insurance-type-block input[type="radio"] + label {
    font-size: 14px;
    padding: 10px 15px;
  }
}
.insurance-main-form .insurance-type-block input[type="radio"]:checked + label {
  background-color: #ED462F;
  color: white;
}
.insurance-main-form .insurance-number-block {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 100%;
  margin-bottom: 40px;
}
@media screen and (max-width: 840px) {
  .insurance-main-form .insurance-number-block {
    margin-bottom: 20px;
  }
}
.insurance-main-form .insurance-number-block .form-group {
  width: calc(55% - 12px);
}
@media screen and (max-width: 840px) {
  .insurance-main-form .insurance-number-block .form-group {
    width: 100%;
    margin-bottom: 15px;
  }
}
.insurance-main-form .insurance-number-block .insurance-search-wrapper {
  z-index: 10;
  width: calc(45% - 12px);
}
@media screen and (max-width: 840px) {
  .insurance-main-form .insurance-number-block .insurance-search-wrapper {
    width: 100%;
  }
}
.insurance-main-form .insurance-services-block {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 40px;
}
@media screen and (max-width: 840px) {
  .insurance-main-form .insurance-services-block {
    margin-bottom: 20px;
  }
}
.insurance-main-form .insurance-services-block h5 {
  margin-top: 0;
  margin-bottom: 20px;
}
@media screen and (max-width: 840px) {
  .insurance-main-form .insurance-services-block h5 {
    margin-bottom: 10px;
  }
}
@media screen and (max-width: 500px) {
  .insurance-main-form .insurance-services-block h5 {
    font-size: 14px;
  }
}
.insurance-main-form .insurance-services-block .insurance-services-list {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-top: 25px;
  padding: 0 22px;
}
@media screen and (max-width: 500px) {
  .insurance-main-form .insurance-services-block .insurance-services-list {
    margin-top: 15px;
    padding: 0;
  }
}
.insurance-main-form .insurance-services-block .insurance-services-list .service-item {
  position: relative;
  width: calc(50% - 14px);
  font-size: 14px;
  margin-bottom: 10px;
}
@media screen and (max-width: 500px) {
  .insurance-main-form .insurance-services-block .insurance-services-list .service-item {
    width: 100%;
    text-align: center;
    position: static;
  }
}
.insurance-main-form .insurance-services-block .insurance-services-list .service-item > img {
  position: absolute;
  left: -24px;
  top: 2px;
  width: 18px;
  height: 18px;
}
@media screen and (max-width: 500px) {
  .insurance-main-form .insurance-services-block .insurance-services-list .service-item > img {
    position: relative;
    margin-right: 10px;
    left: 0;
  }
}
.insurance-main-form .insurance-services-block .insurance-services-list .service-item span {
  font-size: 14px;
}
.insurance-main-form .insurance-search-wrapper {
  position: relative;
  min-height: 35px;
}
.insurance-main-form .insurance-search-wrapper .insurance-search {
  position: absolute;
  z-index: 1;
  width: 100%;
  max-height: calc(39px * 3 + 35px);
  min-height: 35px;
  background: white;
  overflow: hidden;
  padding: 0;
  border: 1px solid #868686;
  border-radius: 15px;
  font-size: 14px;
}
.insurance-main-form .insurance-search-wrapper .insurance-search label {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.insurance-main-form .insurance-search-wrapper .insurance-search label img {
  margin-left: 20px;
}
.insurance-main-form .insurance-search-wrapper .insurance-search label input[type="search"] {
  display: block;
  width: 100%;
  min-height: 35px;
  padding: 0 10px 0 20px;
  outline: none;
  border: none;
}
.insurance-main-form .insurance-search-wrapper .insurance-search label input[type="search"]::-webkit-input-placeholder {
  font-size: 14px;
}
.insurance-main-form .insurance-search-wrapper .insurance-search label input[type="search"].with-img {
  padding: 0 10px;
}
.insurance-main-form .insurance-search-wrapper .insurance-search .search-list {
  list-style: none;
  padding: 0;
  margin: 0;
  max-height: 0;
}
.insurance-main-form .insurance-search-wrapper .insurance-search .search-list.scroll-y {
  overflow-y: auto;
}
.insurance-main-form .insurance-search-wrapper .insurance-search .search-list li {
  height: 39px;
  padding: 0 20px;
  cursor: pointer;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.insurance-main-form .insurance-search-wrapper .insurance-search .search-list li img {
  height: 25px;
  width: auto;
  margin-right: 11px;
}
.insurance-main-form .insurance-search-wrapper .insurance-search .search-list li span {
  font-size: 14px;
}
.insurance-main-form .insurance-search-wrapper .insurance-search .search-list li:hover {
  background-color: #66D1C6;
  color: white;
}
.insurance-main-form .insurance-search-wrapper .insurance-search.collapsed {
  border-color: #66D1C6;
  outline: none;
}
.insurance-main-form .insurance-search-wrapper .insurance-search.collapsed .search-list {
  max-height: calc(39px * 3);
}
.insurance-main-form .insurance-search-wrapper .insurance-search.valid {
  border-color: #868686;
}
.insurance-main-form .insurance-search-wrapper .insurance-search.valid + .info {
  color: black;
  display: block;
}
.insurance-main-form .insurance-search-wrapper .insurance-search + .info {
  margin-top: 43px;
}
@media screen and (min-width: 840px) {
  .insurance-main-form .insurance-search-wrapper .insurance-search + .info {
    height: 16px;
  }
}
.insurance-button-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}
footer {
  margin-top: 30px;
  opacity: 0.5;
}
